import {genCachedGraphQL} from './GraphQL.js';

export default function genCurrentUser() {
  return new Promise(resolve =>
    genCachedGraphQL('2225174527520692', null, 604800, data => {
      const user = data.viewer.account_user.intern_user;
      resolve({
        profilePicture: user.profile_picture.uri,
        fbid: user.unencoded_id,
        firstName: user.first_name
      });
    })
  );
}
