import {genCachedGraphQL} from './GraphQL.js';

export default function genUserPortal() {
  return new Promise(resolve =>
    genCachedGraphQL('4380198408664495', null, 604800, data => {
      resolve(
        (data.viewer.account_user.active_owned_alohas || []).map(a => ({
          serialNumber: a.aloha_device.aloha_device_serial_number
        }))
      );
    })
  );
}
