/* global chrome */

export function genv(keys) {
  return new Promise(resolve => chrome.storage.local.get(keys, resolve));
}

export async function gen(key) {
  const items = await genv([key]);
  return items[key];
}

export function genSetObject(obj) {
  return new Promise(resolve => chrome.storage.local.set(obj, resolve));
}

export async function genSet(key, value) {
  return await genSetObject({[key]: value});
}

export function genClear(keys) {
  return new Promise(resolve => chrome.storage.local.remove(keys, resolve));
}

const KEY_PREFIX = 'calendarext/settings/';

// Default to the user's locale
export const DEFAULT_24_HOUR = !new Date().toLocaleTimeString().endsWith('M');

export const storageKeys = {
  bookmarks: KEY_PREFIX + 'bookmarks',
  clocks: KEY_PREFIX + 'clocks',
  clockSettings: KEY_PREFIX + 'clocksettings',
  theme: KEY_PREFIX + 'theme',
  search: KEY_PREFIX + 'search',
  calendar: KEY_PREFIX + 'calendar',
  calendarFullDay: KEY_PREFIX + 'calendarFullDay',
  use24Hour: KEY_PREFIX + '24hour',
  earlyMeeting: KEY_PREFIX + 'earlymeeting'
};
