import React, {useState, useEffect} from 'react';
import styled from 'styled-components';
import Settings from './Settings.js';
import useGK from './useGK.js';
import genLeftNav from './genLeftNav.js';
import Tippy from '@tippyjs/react';

const NavBar = styled.div`
  display: flex;
  flex-direction: column;
  grid-column: 1;
  grid-row: 2 / 4;
  .searchFocused & {
    display: none;
  }
`;

const NavBarItem = styled.a`
  display: flex;
  padding: 10px;
  opacity: 0.8;
  &:hover {
    opacity: 1;
  }
`;

const NavBarItemImage = styled.img`
  width: 16px;
  height: 16px;
  filter: var(--black-icon-filter);
`;

export default function LeftNav() {
  const homeGK = useGK('intern_home');
  const [items, setItems] = useState(null);
  useEffect(() => {
    genLeftNav(setItems);
  }, []);

  if (!homeGK || items == null || items.length === 0) {
    return <Settings />;
  }

  return (
    <NavBar>
      {items.map(item => (
        <Tippy content={item.label} key={item.label} placement="right">
          <NavBarItem href={item.uri}>
            <NavBarItemImage src={item.icon} />
          </NavBarItem>
        </Tippy>
      ))}
      <Settings />
    </NavBar>
  );
}
