import React from 'react';
import classNames from 'classnames';
import styled from 'styled-components';
import getSearchSuggestions from './getSearchSuggestions.js';
import genInternSearch from './genInternSearch.js';
import {storageKeys} from './LocalStorage.js';
import useStateWithSyncStorage from './useStateWithSyncStorage.js';
import genUserQuerySuggestions from './genUserQuerySuggestions.js';
import SearchResultText from './SearchResultText.js';
import SearchResultIcon from './SearchResultIcon.js';
import CloseButton from './CloseButton.js';
import genLog from './genLog.js';
import {getDescription} from './BunnyCommands.js';
import getInternURL from './getInternURL.js';

const SearchInputWrapper = styled.div`
  position: relative;
`;

const SearchInput = styled.input`
  background: transparent;
  border: 0;
  border-bottom: 2px solid var(--secondary-color);
  font-size: 24px;
  padding: 10px 0;
  color: var(--primary-color);
  width: 600px
  outline: 0;
  font-weight: 300;
  &::placeholder {
    color: var(--secondary-color);
  }
`;

const SearchIcon = styled.img`
  position: absolute;
  right: 0;
  top: 14px;
  height: 20px;
  width: 20px;
  filter: var(--icon-filter);
`;

const SuggestionList = styled.div`
  display: flex;
  flex-direction: column;
  padding-bottom: 8px;
`;

const Suggestion = styled.a`
  color: var(--secondary-color);
  font-size: 20px;
  line-height: 30px;
  font-weight: 300;
  cursor: pointer;
  padding: 8px;
  margin-left: -8px;
  border-radius: 8px;
  &:hover {
    color: var(--primary-color);
    background-color: var(--highlight-background);
    text-decoration: none;
  }
  &:focus {
    background-color: var(--highlight-background);
    outline: 0;
  }
`;

const BunnyDescription = styled.div`
  font-size: 16px;
`;

const ResultList = styled.div`
  display: flex;
  flex-direction: column;
  max-height: 75vh;
  overflow: auto;
  width: 632px;
  position: absolute;
  top: 70px;
  left: 12px;
  transition: all 0.25s ease-in-out;
`;

const Result = styled.a`
  margin: 8px 24px 4px 0px;
  color: var(--primary-color);
  cursor: pointer;
  padding: 4px 8px 8px 8px;
  &:hover {
    background-color: var(--highlight-background);
    text-decoration: none;
  }
  &:focus {
    background-color: var(--highlight-background);
    outline: 0;
  }
  display: flex;
  align-items: center;
  flex-shrink: 0;
  border-radius: 12px;
  min-height: 64px;
`;

const ResultTitle = styled.div`
  font-size: 20px;
  line-height: 30px;
`;

const ResultBody = styled.div`
  font-size: 16px;
  line-height: 24px;
`;

const ViewMoreLink = styled.a`
  font-weight: bold;
  left: 435px;
  top: calc(75vh + 75px);
  position: absolute;
  color: var(--primary-color);
`;

export default function Search() {
  const [text, setText] = React.useState('');
  const [resultsText, setResultsText] = React.useState(false);
  const [results, setResults] = React.useState({});
  const [userSuggestions, setUserSuggestions] = React.useState({});
  const [hideSuggestions, setHideSuggestions] = React.useState(false);
  const [searchEnabled] = useStateWithSyncStorage(storageKeys.search, true);

  React.useEffect(() => {
    document
      .getElementById('app')
      .classList.toggle(
        'searchFocused',
        text.length > 0 || resultsText.length > 0
      );
  }, [text, resultsText]);

  const suggestions = React.useMemo(() => getSearchSuggestions(text), [text]);

  React.useEffect(() => {
    if (text.length > 0) {
      genInternSearch(text, result =>
        setResults(oldResults => ({
          ...oldResults,
          [text]: result
        }))
      );
      genUserQuerySuggestions(result => {
        const suggests = [];
        for (let str of result) {
          if (str.startsWith(text) && str !== text) {
            suggests.push(str);
          }
        }
        setUserSuggestions(oldSuggestions => ({
          ...oldSuggestions,
          [text]: suggests
        }));
      });
      genLog('search_fetch', text);
    }
  }, [text]);

  if (searchEnabled === false) {
    return null;
  }

  const allSuggestions =
    userSuggestions[text] !== undefined
      ? userSuggestions[text]
          .concat(
            suggestions.filter(query => !userSuggestions[text].includes(query))
          )
          .slice(0, 10)
      : suggestions;

  const bunny = getDescription(text.replace(/ .+/, '').trim());
  const bunnyURL = getInternURL('/bunny/?q=' + encodeURIComponent(text));

  return (
    <div className={classNames({searchWrapper: true})}>
      <div className="searchCloseButton">
        <CloseButton
          onClick={() => {
            setText('');
            setResultsText('');
          }}
        />
      </div>
      <div
        className={classNames({
          searchWithSuggestions: true,
          searchHasText: text.length > 0 || resultsText.length > 0
        })}>
        <SearchInputWrapper>
          <SearchInput
            type="text"
            value={text}
            placeholder="Search..."
            onChange={e => {
              setText(e.target.value);
              setHideSuggestions(false);
            }}
            onFocus={() => {
              setHideSuggestions(false);
            }}
            onKeyUp={e => {
              if (e.keyCode === 13) {
                // enter
                if (bunny) {
                  genLog('bunny_enter', text);
                  window.location.href = bunnyURL;
                } else {
                  setResultsText(text);
                  setHideSuggestions(true);
                  genLog('search_enter', text);
                }
              } else if (e.keyCode === 27) {
                // escape
                setText('');
                setResultsText('');
                e.target.blur();
                genLog('search_escape');
              }
            }}
          />
          <SearchIcon src="/search-icon.png" />
        </SearchInputWrapper>
        {hideSuggestions ? null : (
          <SuggestionList>
            {bunny ? (
              <Suggestion
                href={bunnyURL}
                key="bunny"
                onClick={() => {
                  genLog('bunny_click', text);
                }}>
                <span>Bunny shortcut: {text}</span>
                <BunnyDescription>{bunny}</BunnyDescription>
              </Suggestion>
            ) : null}
            {allSuggestions.map(s => (
              <Suggestion
                href="#"
                key={s}
                onClick={() => {
                  setText(s);
                  setResultsText(s);
                  setHideSuggestions(true);
                  genLog('suggestion_click', s);
                }}>
                {s}
              </Suggestion>
            ))}
          </SuggestionList>
        )}
      </div>
      {resultsText && results[resultsText] ? (
        <ResultList>
          {(results[resultsText] || []).filter(Boolean).map((r, idx) => (
            <Result key={idx} href={r.uri}>
              <SearchResultIcon type={r.document_type} employee={r.employee} />
              <div>
                <ResultTitle>
                  <SearchResultText text={r.title} />
                </ResultTitle>
                <ResultBody>
                  <SearchResultText text={r.summary} />
                </ResultBody>
              </div>
            </Result>
          ))}
        </ResultList>
      ) : null}
      {resultsText && results[resultsText] ? (
        <ViewMoreLink
          href={getInternURL(
            '/search/?is_power_search=0&query=' +
              encodeURIComponent(resultsText)
          )}>
          View Results in Intern Search
        </ViewMoreLink>
      ) : null}
    </div>
  );
}
