import useStateWithSyncStorage from './useStateWithSyncStorage.js';
import {storageKeys, DEFAULT_24_HOUR} from './LocalStorage.js';
import React from 'react';
import moment from 'moment-timezone';

function Timestamp(props) {
  const [use24Hour] = useStateWithSyncStorage(
    storageKeys.use24Hour,
    DEFAULT_24_HOUR
  );
  const time = (props.tz
    ? moment(props.time).tz(props.tz)
    : moment(props.time)
  ).format(use24Hour ? 'H:mm' : 'h:mm A');
  if (props.split) {
    const parts = time.split(' ');
    return (
      <span>
        {parts[0]}
        <span className="timeAMPM secondaryColor">
          {parts.length >= 2 ? parts[1] : null}
        </span>
      </span>
    );
  }
  return time;
}

export default Timestamp;
