function getSearchSuggestionForPrefix(prefix) {
  if (top_queries[prefix] !== undefined) {
    return top_queries[prefix];
  }
  return [];
}

export default function getSearchSuggestions(text) {
  let s = '';
  const results = [];
  for (let i = 0; i < text.length; ++i) {
    s += text[i];
    for (let str of getSearchSuggestionForPrefix(s)) {
      if (str.startsWith(text) && str !== text) {
        results.push(str);
        if (results.length >= 10) {
          return results;
        }
      }
    }
  }
  return results;
}
