/* global chrome */

import xhr from './xhr.js';
import genCSRFToken from './genCSRFToken.js';
import {genGraphQL} from './GraphQL.js';
import genCurrentUser from './genCurrentUser.js';
import getInternURL from './getInternURL.js';

let _hasInit = false;

function _copyToClipboard(shortlink) {
  const el = document.createElement('textarea');
  el.value = shortlink;
  el.setAttribute('readonly', '');
  el.style.position = 'absolute';
  el.style.left = '-9999px';
  document.body.appendChild(el);
  el.select();
  document.execCommand('copy');
  document.body.removeChild(el);
}

function toggleNotice(shouldShow) {
  document.getElementById('app').classList.toggle('isUploading', shouldShow);
}

async function _uploadFile(file) {
  toggleNotice(true);
  const csrf = await genCSRFToken();
  const user = await genCurrentUser();
  const url = getInternURL(
    '/pixelcloud/upload/generic?fb_dtsg=' + csrf + '&__a=1&__user=' + user.fbid
  );
  const formData = new FormData();
  formData.append('file', file);
  const result = await xhr(url, formData);
  const json = JSON.parse(result.slice(9));
  const fileID = json.payload.file_id;
  const response = await genGraphQL(1431601080203855, {
    input: {
      client_mutation_id: 1,
      actor_id: user.fbid,
      client_id: 'web',
      title: null,
      description_rte: JSON.stringify({
        blocks: [
          {
            key: 'fe5md',
            text: '',
            type: 'unstyled',
            depth: 0,
            inlineStyleRanges: [],
            entityRanges: [],
            data: {}
          }
        ],
        entityMap: {}
      }),
      files: [fileID],
      file_names: [file.name],
      file_types: [file.type],
      listed: true,
      tags: [],
      text_tags: [],
      tasks: [],
      diffs: [],
      projects: []
    }
  });
  const shortURL = response.pixelcloud_post_create.post.short_url;
  _copyToClipboard(shortURL);
  chrome.notifications.create(
    'pixelcloud_upload_completed',
    {
      type: 'basic',
      title: 'pixelcloud was copied to your clipboard',
      message: shortURL,
      iconUrl: 'icon.png',
      isClickable: false
    },
    function(notification_id) {
      toggleNotice(false);
      // clear the notification after 3 seconds
      window.setTimeout(function() {
        chrome.notifications.clear(notification_id, function() {});
      }, 3000);
    }
  );
}

export default function init() {
  if (_hasInit) {
    return;
  }
  window.addEventListener('paste', e => {
    const items = e.clipboardData.items;
    if (!items) {
      return;
    }
    const files = Array.from(items)
      .map(item => item.getAsFile())
      .filter(Boolean);
    if (files.length === 0) {
      return;
    }
    _uploadFile(files[0]);
  });
  const root = document.getElementById('root');
  ['dragenter', 'dragover', 'dragleave'].forEach(event => {
    root.addEventListener(event, e => {
      e.preventDefault();
      e.stopPropagation();
    });
  });
  root.addEventListener('drop', e => {
    const files = e.dataTransfer.files;
    if (!files || files.length === 0) {
      return;
    }
    e.preventDefault();
    e.stopPropagation();
    _uploadFile(files[0]);
  });

  _hasInit = true;
}
