export function getLeftPercent(date, fullDay) {
  const LENGTH = (fullDay ? 24 : 12) * 3600 * 1000;
  const startOfDay = new Date(date);
  startOfDay.setSeconds(0);
  startOfDay.setMinutes(0);
  startOfDay.setHours(fullDay ? 0 : 7);
  return (100 * (date - startOfDay)) / LENGTH;
}

export function getLeft(date, fullDay) {
  const left = Math.max(0, getLeftPercent(date, fullDay)) + '%';
  if (date.getMinutes() === 0) {
    return 'calc(' + left + ' + 3px)';
  }
  return left;
}

export function getRight(date, fullDay) {
  const LENGTH = (fullDay ? 24 : 12) * 3600 * 1000;
  const endOfDay = new Date(date);
  endOfDay.setSeconds(fullDay ? 59 : 0);
  endOfDay.setMinutes(fullDay ? 59 : 0);
  endOfDay.setHours(fullDay ? 23 : 19);
  const right = Math.max(0, (100 * (endOfDay - date)) / LENGTH) + '%';
  if (date.getMinutes() === 0) {
    return 'calc(' + right + ' + 3px)';
  }
  return right;
}
