import {useEffect, useState} from 'react';
import {genCachedGraphQL} from './GraphQL.js';

export default function useGK(gk_name, defaultValue = false) {
  const [passes, setPasses] = useState(defaultValue);
  useEffect(() => {
    genCachedGraphQL('2150285835052187', {gk_name}, 3600, data => {
      setPasses(!!data.intern_new_tab_gk_check.result);
    });
  }, [gk_name]);
  return passes;
}
