// Modified from https://fburl.com/diffusion/udfyd88k
export default function getBlueJeansLink(id, code, fbSubodmain = false) {
  const protocol = 'https';
  const domain = fbSubodmain ? 'fb.bluejeans.com' : 'bluejeans.com';
  const path = code == null ? `/${id}` : `/${id}/${code}`;

  return `${protocol}://${domain}${path}`;
}
