import getBlueJeansLink from './getBlueJeansLink.js';
import styled from 'styled-components';
import React from 'react';
import genCurrentUser from './genCurrentUser.js';
import genUserPortal from './genUserPortal.js';
import {genGraphQL} from './GraphQL.js';

const ButtonGroup = styled.div`
  display: grid;
  grid-column-gap: 8px;
  grid-template-columns: auto auto;
  margin-top: 8px;
`;

const Button = styled.a`
  display: flex;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 8px 12px;
  border-radius: 6px;
  &:hover {
    background-color: rgba(255, 255, 255, 0.1);
    text-decoration: none;
  }
`;

async function _joinPortal(fbid) {
  await genGraphQL('2842150102541574', {
    fbid
  });
}

async function _joinPortalRoom(room_link) {
  const [user, portals] = await Promise.all([
    genCurrentUser(),
    genUserPortal()
  ]);
  await genGraphQL('3326753404031065', {
    input: {
      actor_id: user.fbid,
      client_mutation_id: 1,
      room_link,
      portal_serial_number: portals[0].serialNumber
    }
  });
}

export default function JoinMeetingButton(props) {
  const blueJeansCredentials = props.event.bluejeans_credentials;
  const conference = props.event.conference;

  const blueJeansID = blueJeansCredentials ? blueJeansCredentials.id : null;
  const oneVcMeeting = props.event.onevc_meeting;
  const oneVcID = oneVcMeeting ? oneVcMeeting.id : null;
  const oneVcLink = conference ? conference.joinable_link : null;
  const portalRoomsLink =
    conference != null
      ? conference.mode === 'EMPLOYEE_ROOMS'
        ? conference.joinable_link
        : null
      : null;

  let uri = null;
  if (oneVcLink != null) {
    uri = oneVcLink;
  } else if (blueJeansID != null) {
    uri = getBlueJeansLink(blueJeansID);
  }

  if (uri == null && oneVcID == null) {
    return null;
  }

  return (
    <>
      <ButtonGroup>
        {uri ? <Button href={uri}>Join Meeting</Button> : null}
        {oneVcID ? (
          <Button onClick={() => _joinPortal(oneVcID)} href="#">
            Join from Portal
          </Button>
        ) : portalRoomsLink != null ? (
          <Button onClick={() => _joinPortalRoom(portalRoomsLink)} href="#">
            Join from Portal
          </Button>
        ) : null}
      </ButtonGroup>
    </>
  );
}
