import React from 'react';
import getInternURL from './getInternURL.js';
import Timestamp from './Timestamp.js';
import getDayLabel from './getDayLabel.js';
import useStateWithSyncStorage from './useStateWithSyncStorage.js';
import {storageKeys} from './LocalStorage.js';
import EventPopover from './EventPopover.js';
import Tippy from '@tippyjs/react';

function Placeholder() {
  return (
    <div className="upNextWrapper upNextWrapperHidden">
      <div className="upNext">
        <div className="upNextLabel">Placeholder</div>
        <div>Placeholder</div>
        <div>Room Placeholder</div>
      </div>
    </div>
  );
}

export default function UpNext(props) {
  const [calendarEnabled] = useStateWithSyncStorage(storageKeys.calendar, true);
  if (!calendarEnabled) {
    return null;
  }

  if (props.event == null) {
    return <Placeholder />;
  }
  let next = 'Up Next at ';
  const now = new Date();
  const then = new Date(props.event.start_time * 1000);
  if (then.getTime() < now.getTime()) {
    next = 'In Progress since ';
  }
  let dayDiff = (then.getTime() - now.getTime()) / 86400000;
  if (then.getDate() !== now.getDate()) {
    dayDiff = Math.ceil(dayDiff);
  } else {
    dayDiff = 0;
  }
  if (dayDiff >= 1 && dayDiff < 2) {
    next = 'Tomorrow at ';
  } else if (dayDiff >= 1 && dayDiff <= 6) {
    next = getDayLabel(then);
  } else if (dayDiff > 6) {
    return <Placeholder />;
  }
  const rooms = props.event.rooms || [];
  const room = rooms.length > 0 ? rooms[0] : null;
  let roomLink;
  if (room != null) {
    roomLink = room.display_name;
    const id = room.room && room.room.id;
    if (id != null) {
      roomLink = (
        <a href={getInternURL('/room_profile/?id=' + id)}>{roomLink}</a>
      );
    }
    roomLink = <span> in: {roomLink}</span>;
  }
  return (
    <div className="upNextWrapper primaryColor">
      <Tippy
        className="tippyPopover"
        content={<EventPopover event={props.event} />}
        placement="top"
        interactive={true}>
        <div className="upNext">
          <div className="upNextLabel">
            {next}
            <Timestamp time={then} />
            {':'}
          </div>
          <div>
            {props.event.title} {roomLink}
          </div>
        </div>
      </Tippy>
    </div>
  );
}
