import {genClearGraphQL, genGraphQL, genCachedGraphQL} from './GraphQL.js';
import {useEffect, useState} from 'react';
import React from 'react';
import styled from 'styled-components';
import RightPaneButton from './RightPaneButton.js';
import CloseButton from './CloseButton.js';
import genLog from './genLog.js';
import ImageWithIcon from './ImageWithIcon.js';

const Menu = styled.div`
  background-color: var(--overlay-background);
  color: var(--primary-color);
  border-radius: 10px;
  padding: 10px 12px;
  max-height: calc(100vh - 150px);
  overflow: auto;
`;

const Divider = styled.div`
  height: 1px;
  width: 100%;
  min-width: 200px;
  background-color: var(--divider-color);
  margin: 8px 0;
`;

const MenuHeader = styled.div`
  display: flex;
  justify-content: space-between;
  font-size: 16px;
`;

const Wrapper = styled.a`
  display: flex;
  align-items: center;
  border-radius: 8px;
  padding: 8px;
  :hover {
    background-color: var(--highlight-background);
    text-decoration: none;
  }
`;

const EmptyState = styled.div`
  width: 232px;
  padding: 4px 0 4px 0;
  font-size: 14px;
`;

const Title = styled.div`
  color: var(--primary-color);
  word-break: break-word;
`;

const Content = styled.div`
  max-width: 200px;
  display: flex;
  flex-direction: column;
  margin: 0 8px;
`;

const Snippet = styled.div`
  word-break: break-word;
  color: var(--secondary-color);
  -webkit-line-clamp: 3;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
`;

const DATA_FBID = '3203339969716828';

function _markAsRead(id) {
  genGraphQL('3111502985595992', {id, is_unread: false});
}

function clearCache() {
  genClearGraphQL(DATA_FBID, {type: 'UNREAD'});
}

function Conversations({isOpen, onOpen, onClose}) {
  const [data, setData] = useState(null);
  useEffect(() => {
    genCachedGraphQL(
      DATA_FBID,
      {type: 'UNREAD'},
      3600,
      data => {
        setData(data.intern_home_conversations ?? []);
      },
      true
    );
  }, []);

  return (
    <>
      {isOpen ? (
        <Menu>
          <MenuHeader>
            Conversations
            <CloseButton onClick={onClose} />
          </MenuHeader>
          {data == null || data.length === 0 ? (
            <>
              <Divider />
              <EmptyState>No unread conversations.</EmptyState>{' '}
            </>
          ) : (
            <>
              <Divider />
              {data.map(item => (
                <Wrapper
                  key={item.id}
                  href={item.uri}
                  onMouseUp={e => {
                    // Handle middle click since onClick isn't triggered for it
                    if (e.button === 1) {
                      // Use a small delay so it has time to open the new tab before removing this item
                      setTimeout(() => {
                        _markAsRead(item.id);
                        setData(d => d.filter(x => x.id !== item.id));
                        clearCache();
                      }, 500);
                      genLog('conversations_middle_click');
                    }
                  }}
                  onClick={e => {
                    if (e.button === 0) {
                      _markAsRead(item.id);
                      setData(d => d.filter(x => x.id !== item.id));
                      clearCache();
                    }
                    if (e.shiftKey) {
                      e.preventDefault();
                      e.stopPropagation();
                      genLog('conversations_shift_click');
                    } else {
                      genLog('conversations_click');
                    }
                  }}>
                  <ImageWithIcon
                    srcs={item.profile_pictures.map(p => p.uri)}
                    icon={item.icon.uri}
                  />
                  <Content>
                    <Title>{item.title}</Title>
                    <Snippet>
                      {item.actor && item.actor.full_name !== item.title
                        ? item.actor.full_name + ': '
                        : ''}
                      {item.snippet}
                    </Snippet>
                  </Content>
                </Wrapper>
              ))}
            </>
          )}
        </Menu>
      ) : (
        <RightPaneButton
          onClick={() => {
            if (isOpen) {
              onClose();
            } else {
              onOpen();
            }
          }}
          icon="/comment.png"
          label="Conversations"
        />
      )}
    </>
  );
}

export default Conversations;
