import React from 'react';
import styled, {css} from 'styled-components';
import Timestamp from './Timestamp.js';
import useStateWithSyncStorage from './useStateWithSyncStorage.js';
import {storageKeys} from './LocalStorage.js';
import {useState} from 'react';
import Switch from './Switch.js';
import moment from 'moment-timezone';
import CloseButton from './CloseButton.js';
import Tippy from '@tippyjs/react';

const ClocksWrapper = styled.div`
  display: inline-flex;
  flex-direction: row;
  margin-top: 8px;
  margin-right: 8px;
  cursor: pointer;
  padding: 8px 16px;
  border-radius: 10px;
  &:hover {
    background-color: var(--overlay-background);
  }
  grid-column: 2 / -1;
  grid-row: 2;
  white-space: nowrap;
  justify-self: flex-end;
  .searchFocused & {
    display: none;
  }
`;

const ResetToDefault = styled.a`
  color: var(--secondary-color);
`;

const ClockWrapper = styled.div`
  font-weight: 300;
  text-align: right;
  margin-left: 24px;
  line-height: 20px;
  font-size: 16px;
  color: var(--secondary-color);
`;

const EditWrapper = styled.div`
  text-align: right;
  margin-top: 2px;
  color: var(--secondary-color);
  font-size: 10px;
  line-height: 14px;
  letter-spacing: 1;
  font-weight: 700;
  opacity: 0;
  ${ClocksWrapper}:hover & {
    opacity: 1;
  }
`;

const ClockLocation = styled.div`
  font-size: 10px;
  line-height: 16px;
  letter-spacing: 1;
  font-weight: 700;
`;

const Menu = styled.div`
  background-color: var(--overlay-background);
  color: var(--primary-color);
  border-radius: 10px;
  padding: 10px 12px;
  max-height: 80vh;
  overflow: auto;
`;

const LongDivider = styled.div`
  background: var(--secondary-color);
  opacity: 0.2;
  height: 1px;
  margin: 8px -12px;
`;

const Divider = styled.div`
  height: 1px;
  width: 100%;
  min-width: 200px;
  background-color: var(--divider-color);
  margin: 8px 0;
`;

const MenuHeader = styled.div`
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  font-weight: bold;
`;

const Setting = styled.div`
  align-items: center;
  display: flex;
  font-size: 12px;
  justify-content: space-between;
  margin-top: 4px;
  ${props =>
    props.isDisabled &&
    css`
      color: var(--secondary-color);
    `};
`;

const Tray = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const ClockSettingCode = styled.div`
  font-size: 10px;
  line-height: 14px;
  letter-spacing: 1;
  font-weight: 700;
`;

const ClockSettingLabel = styled.div`
  flex-grow: 1;
  margin-left: 12px;
`;

const Icon = styled.img`
  width: 16px;
  height: 16px;
  filter: var(--icon-filter);
`;

function Clock(props) {
  const {city, timezone_name} = props.city;
  return (
    <ClockWrapper>
      <Timestamp time={props.now} tz={timezone_name} />
      <br />
      <ClockLocation>{city}</ClockLocation>
    </ClockWrapper>
  );
}

const CLOCKS = [
  {
    timezone_name: 'America/Los_Angeles',
    city: 'MPK',
    label: 'Menlo Park'
  },
  {
    timezone_name: 'America/Denver',
    city: 'DEN',
    label: 'Denver'
  },
  {
    timezone_name: 'America/Chicago',
    city: 'AUS',
    label: 'Austin'
  },
  {
    timezone_name: 'America/New_York',
    city: 'NYC',
    label: 'New York'
  },
  {
    timezone_name: 'America/Sao_Paulo',
    city: 'SAO',
    label: 'Sao Paulo'
  },
  {
    timezone_name: 'Europe/Dublin',
    city: 'DUB',
    label: 'Dublin'
  },
  {
    timezone_name: 'Europe/London',
    city: 'LON',
    label: 'London'
  },
  {
    timezone_name: 'UTC',
    city: 'UTC',
    label: 'UTC'
  },
  {
    timezone_name: 'Europe/Zurich',
    city: 'ZUR',
    label: 'Zurich'
  },
  {
    timezone_name: 'Asia/Jerusalem',
    city: 'TLV',
    label: 'Tel Aviv'
  },
  {
    timezone_name: 'Asia/Kolkata',
    city: 'HYD',
    label: 'Hyderabad'
  },
  {
    timezone_name: 'Asia/Hong_Kong',
    city: 'HOK',
    label: 'Hong Kong'
  },
  {
    timezone_name: 'Asia/Singapore',
    city: 'SIN',
    label: 'Singapore'
  },
  {
    timezone_name: 'Asia/Seoul',
    city: 'SEO',
    label: 'Seoul'
  },
  {
    timezone_name: 'Asia/Tokyo',
    city: 'TOK',
    label: 'Tokyo'
  },
  {
    timezone_name: 'Australia/Sydney',
    city: 'SYD',
    label: 'Sydney'
  }
];

const DEFAULT_ENABLED = ['MPK', 'NYC', 'LON', 'HYD', 'SIN'];

function Clocks(props) {
  const tz = moment.tz.guess();
  const DEFAULT_CLOCKS = CLOCKS.map(c => ({
    ...c,
    enabled: DEFAULT_ENABLED.includes(c.city) && c.timezone_name !== tz
  }));
  const [clocksEnabled] = useStateWithSyncStorage(storageKeys.clocks, true);
  let [clocks, setClocks] = useStateWithSyncStorage(
    storageKeys.clockSettings,
    DEFAULT_CLOCKS
  );
  if (
    !Array.isArray(clocks) ||
    clocks.length === 0 ||
    clocks.filter(c => c.enabled).length === 0
  ) {
    clocks = DEFAULT_CLOCKS;
  }
  // Add any new clocks
  clocks = clocks.concat(
    CLOCKS.filter(c => clocks.find(c2 => c2.city === c.city) == null)
  );
  const [isOpen, setIsOpen] = useState(false);
  if (!clocksEnabled) {
    return null;
  }
  return (
    <Tippy
      visible={isOpen}
      onClickOutside={() => setIsOpen(false)}
      className="tippyPopover"
      content={
        <Menu>
          <MenuHeader>
            <div>World Clocks</div>
            <CloseButton
              onClick={() => {
                setIsOpen(false);
              }}
            />
          </MenuHeader>
          <LongDivider />
          {clocks.map((c, i) => (
            <>
              {i > 0 ? <Divider /> : null}
              <Setting isDisabled={!c.enabled}>
                <ClockSettingCode>{c.city}</ClockSettingCode>
                <ClockSettingLabel>
                  {c.label + ' (' + moment.tz(c.timezone_name).zoneAbbr() + ')'}
                </ClockSettingLabel>
                <Switch
                  toggled={c.enabled}
                  onToggle={() => {
                    // don't allow disabling the last clock
                    if (
                      clocks.filter(c2 => c2.enabled).length === 1 &&
                      clocks.find(c2 => c2.city === c.city).enabled
                    ) {
                      return;
                    }
                    setClocks(
                      clocks.map(c2 =>
                        c2.city === c.city ? {...c2, enabled: !c2.enabled} : c2
                      )
                    );
                  }}
                />
              </Setting>
            </>
          ))}
          <LongDivider />
          <Tray>
            <ResetToDefault
              href="#"
              onClick={() => {
                setClocks(DEFAULT_CLOCKS);
              }}>
              Reset to Default
            </ResetToDefault>
          </Tray>
        </Menu>
      }
      arrow={false}
      placement="bottom-end"
      interactive={true}>
      <ClocksWrapper onClick={() => setIsOpen(true)}>
        <EditWrapper>
          <div>
            <Icon src="/pencil.png" />
          </div>
          EDIT
        </EditWrapper>
        {clocks.map(c =>
          c.enabled ? <Clock now={props.now} city={c} key={c.city} /> : null
        )}
      </ClocksWrapper>
    </Tippy>
  );
}

export default Clocks;
